<template>
	<section class="my-container">
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="表单"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" >查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--列表-->
		<el-table :data="lists" border highlight-current-row header-cell-class-name="table-header" v-loading="listLoading" @selection-change="selsChange" style="width: 100%;text-align: left;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="name" label="名称" width="120" >
			</el-table-column>
			<el-table-column prop="status" label="状态" width="100" :formatter="getStatus">
			</el-table-column>
			<el-table-column prop="desc" label="描述">
			</el-table-column>
			<el-table-column prop="date_entered" label="创建时间" width="160" >
			</el-table-column>
			<el-table-column label="操作" width="210">
				<template slot-scope="scope">
					<el-button type="success" size="mini" @click="handleDesigner(scope.$index, scope.row)">设计</el-button>
					<el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button size="mini" type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	var _this
	export default{
		name: "forms",
		data(){
			return {
				listLoading: false,
				filters: {name:''},
				sels: [], //列表选中列
				total: 1,
				lists: [{id: 1, name: '表单1', status: 1, desc: '表单描述', date_entered: '2022-03-10 12:00:00'}]
			}
		},
		created() {
			_this = this
		},
		methods:{
			handleAdd(){
				_this.$router.push('/form/add')
			},
			getStatus(row, column){
				return row.status==1?'上架':'下架'
			},
			selsChange: function(sels) {
				_this.sels = sels;
			},
			handleEdit: function() {
			
			},
			handleDesigner: function(index, row){
				_this.$router.push('/form/designer')
			},
			handleDel: function() {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					setTimeout(function() {
						_this.listLoading = false
						_this.$message({
							type: 'success',
							message: '删除成功!'
						});
					}, 2000)
				}).catch(() => {
			
				});
			},
			handleCurrentChange: function() {
			
			},
			//批量删除
			batchRemove: function() {
				var ids = _this.sels.map(item => item.id).toString();
				_this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					_this.listLoading = true;
					setTimeout(function() {
						_this.listLoading = false
						_this.$message({
							type: 'success',
							message: '删除成功!'
						});
					}, 2000)
				}).catch(() => {
			
				});
			}
		}
	}
</script>

